html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.player {
  z-index: 10;
  position: fixed;
  bottom: 0;
  left: 0;
}

.player__close {
  display: none;
}

.player .player__close {
  display: block;
}

